import React from "react";
import { NavLink } from "react-router-dom";

const NavBar = () => {
  return (
    <div>
      <h1>NavBar en React</h1>
      <ul className="btn-group mr-5">
        <li>
          <NavLink to="/" className="btn btn-dark active">
            Inicio
          </NavLink>
        </li>
        <li>
          <NavLink to="/formulario" className="btn btn-dark active">
            Formulario
          </NavLink>
        </li>
        <li>
          <NavLink to="/useEffect" className="btn btn-dark active">
            useEffect
          </NavLink>
        </li>
        <li>
          <NavLink to="/firebaseConfig" className="btn btn-dark active">
            Firebase
          </NavLink>
        </li>
      </ul>
    </div>
  );
};

export default NavBar;
