import React, { useState } from "react";
import { Link } from "react-router-dom";

const UseEffect = () => {
  const [listaData, setListaData] = useState([]);

  React.useEffect(() => {
    // ejecuta una accion luego de que se renderice el componente
    let cancel = false;

    const obtenerDatos = async () => {
      if (!cancel) {
        const data = await fetch(
          "https://age-of-empires-2-api.herokuapp.com/api/v1/civilizations"
        );
        const usuarios = await data.json();
        setListaData(usuarios.civilizations);
      } else {
        cancel = true;
      }
    };

    obtenerDatos();
    // cleanup useEffect
    return () => {
      cancel = true;
    };
  }, []);

  return (
    <div>
      <h1>Hook useEffect</h1>
      <h2>lista consumida de una API (Age of Empires)</h2>
      <ul>
        {listaData.map((item) => (
          <li key={item.id}>
            {/* to con una URL dinamica utilizando el template string */}
            <Link to={`/${item.id}`}>
              {item.name} - {item.expansion}
            </Link>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default UseEffect;
