import React, { useState } from "react";
import { nanoid } from "nanoid";

const CrudSimple = () => {
  const [tarea, setTarea] = useState("");
  const [agregadas, setAgregadas] = useState([]);
  // a la hora de crear un state para editar siempre inicializarlo en falso para poder ir modificando con el operador ternario
  const [modoEdicion, setModoEdicion] = useState(false);
  const [id, setId] = useState("");
  const [error, setError] = useState(null);

  const guardarTarea = (e) => {
    e.preventDefault();

    // validar el input de tarea
    if (!tarea.trim()) {
      setError("Campo Vacio!");
      return;
    } else {
      setError("");
    }

    setAgregadas([...agregadas, { id: nanoid(), tarea: tarea }]);

    e.target.reset();
    setTarea("");
  };

  const eliminarTarea = (id) => {
    console.log(id + " a sido eliminado");
    // este array va a capturar los datos que sean distintos al id presionado por el evento
    const arrayFiltrado = agregadas.filter((item) => item.id !== id);
    setAgregadas(arrayFiltrado);
  };

  const editar = (item) => {
    console.log(item);
    setModoEdicion(true);
    setTarea(item.tarea);
    setId(item.id);
  };

  const editarTarea = (e) => {
    e.preventDefault();

    if (!tarea.trim()) {
      setError("Campo Vacio!");
      return;
    } else {
      setError("");
    }

    const arrayEditado = agregadas.map((item) =>
      item.id === id ? { id, tarea: tarea } : item
    );

    setAgregadas(arrayEditado);
    setTarea("");
    setModoEdicion(false);
    setId("");
  };

  return (
    <div className="container mt-5">
      <h1 className="text-center">Crud-simple en React</h1>
      <hr />
      <div className="row">
        <div className="col-8">
          <h4 className="text-center">Lista de tareas</h4>
          <ul className="list-group">
            {
              // operador ternario para renderizar la lista cuando no hay elementos pintados
              agregadas.length === 0 ? (
                <li className="list-group-item">no hay tareas agregadas</li>
              ) : (
                agregadas.map((item) => (
                  <li className="list-group-item" key={item.id}>
                    <span className="lead">{item.tarea}</span>
                    <button
                      className="btn btn-danger btn-sm float-end mx-2"
                      onClick={() => eliminarTarea(item.id)}
                    >
                      Eliminar
                    </button>
                    {/* el boton de editar en su evento onClick tiene que llevar como parametro el item entero */}
                    <button
                      className="btn btn-warning btn-sm float-end"
                      onClick={() => editar(item)}
                    >
                      Editar
                    </button>
                  </li>
                ))
              )
            }
          </ul>
        </div>

        <div className="col-4">
          <h4 className="text-center">
            {modoEdicion ? "Editar Tarea" : "Agregar una tarea"}
          </h4>
          <form onSubmit={modoEdicion ? editarTarea : guardarTarea}>
            {error ? <p className="text-danger">{error}</p> : null}

            <input
              type="text"
              className="form-control mb-2"
              placeholder="ingrese tarea"
              onChange={(e) => setTarea(e.target.value)}
              //limpiar el formulario
              value={tarea}
            />
            {modoEdicion ? (
              <button className="btn btn-warning btn-block" type="submit">
                Editar
              </button>
            ) : (
              <button className="btn btn-dark btn-block" type="submit">
                Agregar
              </button>
            )}
          </form>
        </div>
      </div>
    </div>
  );
};

export default CrudSimple;
