import React, { useState } from "react";
import { firebase } from "../Firebase/firebase";

const FirebaseConfig = () => {
  const [listado, setListado] = useState([]);
  const [tarea, setTarea] = useState("");
  const [modoEdicion, setModoEdicion] = useState(false);
  const [id,setId]=useState('');

  React.useEffect(() => {
    // configurar firebase en React

    const obtenerFirebase = async () => {
      try {
        const dataBase = firebase.firestore();
        const data = await dataBase.collection("Tareas").get();
        const arrayData = await data.docs.map((item) => ({
          id: item.id,
          ...item.data(),
        }));

        setListado(arrayData);
      } catch (error) {
        console.log(error);
      }
    };

    obtenerFirebase();
  }, [tarea]);

  const Agregar = async (e) => {
    e.preventDefault();

    if (!tarea.trim()) {
      alert("elemento vacio");
      return;
    }

    try {
      const dataBase = firebase.firestore();
      const nuevaTarea = {
        tareas: tarea,
        numero: Date.now(),
      };
      // agregar un nuevo documento a firebase
      const data = await dataBase.collection("Tareas").add(nuevaTarea);

      setTarea([...tarea, { ...nuevaTarea, id: data.id }]);

      setTarea("");
    } catch (error) {
      console.log(error);
    }
  };

  const eliminarDeFirebase = async (id) => {
    try {
      console.log(id);
      const dataBase = firebase.firestore();
      // eliminar un documento de firebase
      await dataBase.collection("Tareas").doc(id).delete();
      const arrayFiltrado = listado.filter((item) => item.id !== id);
      setListado(arrayFiltrado);
    } catch (error) {
      console.log(error);
    }
  };

  const Editar = (item) => {
    setModoEdicion(true);
    setTarea(item.tareas);
    setId(item.id);
    
  };

  const editarFirebase = async (e) => {
    e.preventDefault()
    if (!tarea.trim()) {
      alert("elemento vacio");
      return;
    }

    try {
      const dataBase=firebase.firestore()
      // actualizar un documento de firebase
      await dataBase.collection('Tareas').doc(id).update({
        tareas:tarea
      })
      const arrayEditado = listado.map(item=> (item.id===id ?
        { id, tareas:tarea,numero:item.numero}:item) )
      setListado(arrayEditado);
      
      setTarea('');
      setId('');
      setModoEdicion(false);
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div className="container">
      <h2>Crud a traves de firebase</h2>
      <div className="row">
        <div className="col-md-6">
          <ul className="list-group">
            {listado.map((item) => (
              <li className="list-group-item" key={item.id}>
                {item.tareas}-{item.numero}
                <button
                  className="btn btn-danger btn-sm float-end mx-2"
                  onClick={() => eliminarDeFirebase(item.id)}
                >
                  Eliminar
                </button>
                <button
                  className="btn btn-warning btn-sm float-end mx-2"
                  onClick={() => Editar(item)}
                >
                  Editar
                </button>
              </li>
            ))}
          </ul>
        </div>
        <div className="col-md-6">
          <h3>{modoEdicion ? "Editar tarea" : "Agregar tarea"}</h3>
          <form onSubmit={modoEdicion ? editarFirebase : Agregar}>
            <input
              type="text"
              planceholder="Ingrese tarea"
              className="form-control mb-2"
              onChange={(e) => {
                setTarea(e.target.value);
              }}
              value={tarea}
            />
            <button 
            type="submit" 
            className={modoEdicion?"btn btn-warning btn-block":"btn btn-success btn-block"}>
                {modoEdicion? "Editar": "Agregar"}
            </button>
            
          </form>
        </div>
      </div>
    </div>
  );
};

export default FirebaseConfig;
