import React,{useState} from 'react'
import { useParams } from 'react-router-dom'

const Civilizacion = () => {

    const [pueblo, setPueblo] = useState([]);

    
    const {id}= useParams()

    React.useEffect(() => {
      const obtenerDatos = async () => {
        const data = await fetch(
          `https://age-of-empires-2-api.herokuapp.com/api/v1/civilization/${id}`
          );
          const civilizaciones = await data.json();
          setPueblo(civilizaciones);
        };
        
        obtenerDatos();
      }, []);
    
    
  return (

    <div>
        <h2>civilizacion:</h2>
        <h3>{pueblo.name}</h3>
        <h4> Army Type:{pueblo.army_type}</h4>
        <h4> Team Bonus:{pueblo.team_bonus}</h4>
    </div>
  )
}

export default Civilizacion